import { useState, useEffect } from "react";
import React from "react";
import * as M from "../css/StyledNav";
import { useNavigate } from "react-router-dom";

const Nav = () => {
  const navigate = useNavigate();
  const [token, setToken] = useState(localStorage.getItem("access_token")); // localStorage  
  const [userId, setUserId] = useState(null); // :  ID 
  const [showLoginModal, setShowLoginModal] = useState(false); //      

  const goToRemeberTree = async () => {
    if (token) {
      try {
        const response = await fetch('http://127.0.0.1:8000/accounts/api/get-user-id-from-token', {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${token}`, //   
          },
        });
    
        if (response.ok) {
          const data = await response.json();
          const userId = data.userId; // API   ID 
          setUserId(userId); //  ID  
          //  ID     
          console.log('User ID:', userId);

          const treeResponse = await fetch(`http://127.0.0.1:8000/rememberTree/user/${userId}/`, {
            method: 'GET',
            headers: {
              'Authorization': `Bearer ${token}`, //   
            },
          });

          if (treeResponse.ok) {
            const treeData = await treeResponse.json();
            console.log('Remember Tree Data:', treeData);
            //  ID     

            if(treeData.length>0){
              navigate('/rememberTree'); //  
            }else{
              alert(' .');
              navigate('/plantTreeStepOne'); //  
            }
          
          } else {
            navigate('/plantTreeStepOne'); //  
          } 
        } else {
          setShowLoginModal(true); //       
        }
      } catch (error) {
        console.error('Error fetching user ID:', error);
        setShowLoginModal(true); //       
      }

      
      // navigate("/plantTreeStepOne");
    } else {
      setShowLoginModal(true); //      
    }
  };

  return (
    <M.Nav>
      <M.Navbar>
        <M.NavItem>
          <a href="/">HOME</a>
          <hr />
        </M.NavItem>
        <M.NavItem>
          <a href="/memorialHallList"> </a>
          <hr />
        </M.NavItem>
        <M.NavItem>
          <a onClick={goToRemeberTree}> </a>
          <hr />
        </M.NavItem>
        <M.NavItem>
          <a href="/memorialHallSignup">  </a>
          <hr />
        </M.NavItem>
      </M.Navbar>
    </M.Nav>
  );
};

export default Nav;
